
.right-sidebar{
   width: 300px;
   margin: 40px 0px 15px 24px;
   float: right;
   font-size: 15px;
         
} 
.widget{
   margin-top: 10px;
   box-shadow: 3px 3px 10px rgba(0 0 0 / 5%), -3px -3px 10px rgb(0 0 0 / 5%);

}
.widget h4{
   background-color: #fbf3d5;
   margin: 0%;
   padding: 15px;
   border: solid 1px #f1e5bc;
}
.right-sidebar-div-1{
   background-color: #fdf7e2;
   padding: 15px;
   border: solid 1px #f1e5bc;
   
}
.right-sidebar-div-1 {
   display: flex;
   align-items: flex-start;
   justify-content: space-evenly;

}
.right-sidebar-div-1 p{
   padding-left: 9px;
   margin-top: -2%;
   
}
.widget-tags{
   margin-top: 40px;
   box-shadow: 3px 3px 10px rgba(0 0 0 / 5%), -3px -3px 10px rgb(0 0 0 / 5%);
}
.widget-tags h3{
   margin: 0%;
   padding: 15px;
   background-color: #f8f9f9;
   border: solid 1px #e3e6e8;

}
.widget-tags-div{
   display: flex;
   flex-flow: row wrap;
   align-items: center;
   justify-content: space-evenly;
   border: solid 1px #f8f9f9;
   padding: 10px;

}
.widget-tags-div p{
   padding: 6px;
    background-color: #d4e6f3;
   color: #39739d;
   border-radius: 5px;
   font-size: 14px; 
}